var parseString = require('xml2js').parseString;
var objectKeysToLowerCase = require('./objectKeysToLowerCase');
var user = false;

const botMessage = {
"recipient":{
   "id":"USER_ID"
 },
};

const userMessage = {
"sender":{
    "id":"USER_ID"
  },
 }

 function parseButton(input) {
   const output =  {
     title: input._,

   }
   if(input.$.url) {
     output.type = 'web_url';
     output.url = input.$.url;
   }

   if(input.$.payload) {
     output.payload = input.$.payload;
     output.type = 'postback';
   }
   return output;
 }

 function parseTemplate(input) {
   const attachment = {
     type: 'template',
     payload: {
       template_type: input.$.type,
       text: input.Text && input.Text[0],
       buttons: input.Button && input.Button[0] && input.Button.map(parseButton)
     }
   }
   return {
     attachment,
   }
 }


function parseMML(input) {
  let buffer = '';
  const result = [];

  const proccessBuffer = () => {
    parseString(buffer, (e, res) => {
      if(!e && res) {
        if(res.Template) {
          res = parseTemplate(res.Template);
        }
        res = objectKeysToLowerCase(res, true);
        if(!res.postback) {
          result.push(Object.assign({}, user ? userMessage : botMessage, { message: res }));
        } else {

          result.push(Object.assign({}, user ? userMessage : botMessage, { postback: {
            payload: res.postback.payload,
            title: res.postback._
          } }));
        }
      }
    })
  }
  input.split('\n').forEach((line) => {
    line = line.trim();
    if(line[0] === '+' || line[0] === '-') {
      proccessBuffer();
      buffer = '';
      user = line[0] === '-';
      line = line.substring(1);
    }
    buffer += line;
  })
  proccessBuffer();
  return result;
}

module.exports = parseMML;



