import React, { Component } from 'react';
import logo from './logo.svg';
import parseMML from './parseMML';
import './App.css';


function Button({ title }) {
  return <div className="chat_button">{title}</div>
}


function Template({ payload, sent, end }) {
  if(payload.template_type == 'button')  {
    return <li className={"chat__bubble " + (sent ? "chat__bubble--sent" : "chat__bubble--rcvd") + (end ? " chat__bubble--stop" : "")}>
        <div className="text">{payload.text}</div>
      { payload.buttons.map((buttonProps) => <Button title={buttonProps.title} />)}
    </li>
  } else {
    return <li className={"chat__bubble " + (sent ? "chat__bubble--sent" : "chat__bubble--rcvd") + (end ? " chat__bubble--stop" : "")}>
      <div className="text">UNSUPPORTED TEMPLATE</div>
    </li>
  }
}

function Message({ sent, message, postback, next }) {
  const text =  message ? message.text : postback.title;
  let end = false;
  if(!next || (next && next.sent !== sent)) {
    end = true;
  }
  if(text)  {
    return <li className={"chat__bubble " + (sent ? "chat__bubble--sent" : "chat__bubble--rcvd") + (end ? " chat__bubble--stop" : "")}>
      <div className="text">{text}</div>
    </li>
  }
  if(message.attachment) {
    if(message.attachment.type == 'image') {
      return <li className={"chat__bubble " + (sent ? "chat__bubble--sent" : "chat__bubble--rcvd") + (end ? " chat__bubble--stop" : "")}>
        <img style={{maxHeight: 150, borderRadius: 10}} src={message.attachment.payload.url} />
        </li>
    }
    if(message.attachment.type == 'template') {
      return <Template payload={message.attachment.payload} sent={sent} end={end}/>
    }
    return <li className={"chat__bubble " + (sent ? "chat__bubble--sent" : "chat__bubble--rcvd") + (end ? " chat__bubble--stop" : "")}>
      <div className="text">UNSUPPORTED ATTACHMENT</div>
    </li>
  }
  return <li className={"chat__bubble " + (sent ? "chat__bubble--sent" : "chat__bubble--rcvd") + (end ? " chat__bubble--stop" : "")}>
    <div className="text">UNSUPPORTED MESSAGE</div>
  </li>
}

class App extends Component {
  state = {
    text: `+<Text>Hey User</Text>
  -<Text>Hey Bot</Text>
-<Text>How are you doing?</Text>
+<Text>Good</Text>
    -<Text>Can I have a picture of a cat?</Text>
    +<Attachment type="image" src="http://www.warrenphotographic.co.uk/photography/bigs/04286-Cute-cat-and-kitten-white-background.jpg" />
    +<Template type="button">
      <Text>What do you want to do next?</Text>
      <Button url="https://www.messenger.com">Visit Messenger</Button>
      <Button payload="DEVELOPER_DEFINED_PAYLOAD">Bookmark Item</Button>
    </Template>
    -<Postback payload="DEVELOPER_DEFINED_PAYLOAD">Bookmark Item</Postback>
`
  }
  render() {
    const messages = parseMML(this.state.text);
    messages.forEach((m => m.sent = !m.recipient))
    return (
      <div classNameName="App">
        <textarea value={this.state.text} rows="4" cols="50" onChange={(e) => this.setState({ text: e.target.value })}/>
        <ul className="chat">
          {messages.map((m, i) => <Message
            sent={m.sent}
            next={messages[i + 1]}
            message={m.message}
            postback={m.postback}
          />)}
        </ul>
      </div>
    );
  }
}




export default App;



